<template>
  <div>
    <ul v-for="city in cities"  :key="city.name">
      <li class="locations">
        <span >
          <router-link v-bind:to="{name: 'view-city', params: {name: city.name}}" >
            <div class="city hero-bkg-animated ">{{city.name}}</div>
          </router-link>
        </span>
      </li>
    </ul>
  </div>

</template>

<script>
import db from '../components/firebaseInit'

export default {
  data () {
    return {
      cities: []
    }
  },
  created() {
    db.collection('cities').get().then(querySnapshot => {
      querySnapshot.forEach(doc => {
        //console.log(doc.data());
        const data = {
          'name': doc.data().name,
        }
        this.cities.push(data)
      })
    })
  },
}
</script>

