import Vue from 'vue'
import Router from 'vue-router'
import Dashboard from '@/components/Dashboard'
import NewRestaurant from '@/views/NewRestaurant'
import ViewRestaurant from '@/views/ViewRestaurant'
import EditRestaurant from '@/views/EditRestaurant'
import Lisboa from '@/views/Lisboa'
import Barreiro from '@/views/Barreiro'
import NewCity from '@/views/NewCity'
import Cities from '@/views/Cities'
import ViewCity from '../views/ViewCity'

Vue.use(Router)

export default new Router({

  routes: [
    {
      path: '/',
      name: 'dashboard',
      component: Dashboard
    },
    {
      path: '/new',
      name: 'new-restaurant',
      component: NewRestaurant
    },
    {
      path: '/newCity',
      name: 'new-city',
      component: NewCity
    },
    {
      path: '/cities',
      name: 'cities',
      component: Cities,
      meta: { centerlat: 38.717, centerlon: -9.145, filterByCity: 'Lisboa' }
    },
    {
      path: '/lisboa',
      name: 'lisboa',
      component: Lisboa,
      meta: { centerlat: 38.717, centerlon: -9.145, filterByCity: 'Lisboa' }
    },
    {
      path: '/barreiro',
      name: 'barreiro',
      component: Barreiro,
      meta: { centerlat: 38.657091, centerlon: -9.074422, filterByCity: 'Barreiro' }
    },
    {
      path: '/edit/:name',
      name: 'edit-restaurant',
      component: EditRestaurant
    },
    {
      path: '/:name',
      name: 'view-restaurant',
      component: ViewRestaurant,
      meta: { centerlat: 38.657091, centerlon: -9.074422 }
    },
    {
      path: '/:name',
      name: 'view-city',
      component: ViewCity,
      meta: { centerlat: 38.657091, centerlon: -9.074422 }
    }
  ]
})



